<template>
    <div :style="'background:url('+bg+') no-repeat;background-size:cover;height:675px;'">
        <h1 class="main-title"></h1>
        <Row class="write">
            <Col :xs="{ span: 20, offset:2  }" :lg="{ span: 8, offset:8 }">
            <Input v-model="userName" size="large" placeholder=""></Input>
            </Col>
        </Row>
        <Row class="write">
            <Col :xs="{ span: 20, offset:2  }" :lg="{ span: 8, offset:8 }">
            <Input v-model="realName" size="large" placeholder=""></Input>
            </Col>
        </Row>
        <Row class="write">
            <Col :xs="{ span: 20, offset:2  }" :lg="{ span: 8, offset:8 }">
            <Select placeholder="" v-model.sync="genger" size="large">
                <Option v-for="item in sexList" :value="item.title" :key="item.title">{{ item.title }}</Option>
            </Select>
            </Col>
        </Row>
        <Row class="write">
            <Col :xs="{ span: 20, offset:2  }" :lg="{ span: 8, offset:8 }">
            <Input type="password" v-model="password" size="large" placeholder=""></Input>
            </Col>
        </Row>
        <Row class="write">
            <Col :xs="{ span: 20, offset:2  }" :lg="{ span: 8, offset:8 }">
            <Input type="password" v-model="confirmPWD" size="large" placeholder=""></Input>
            </Col>
        </Row>
        <Row class="write">
            <Col :xs="{ span: 20, offset:2  }" :lg="{ span: 8, offset:8 }">
            <Button @click="register" long type="primary" ></Button>
            </Col>
        </Row>
    </div>
</template>

<script>
    import requestObj from '../../api/api';
    export default {
        name: "register",
        data(){
            return {
	            bg:require('../../assets/frontend/c-bg.jpg'),
                userName:'',
                realName:'',
                genger:'',
                password:'',
                confirmPWD:'',
                sexList:[{
                    title:'',
                },{
                    title:'',
                },],
            };
        },
        methods:{
            register(){
                var self=this;
	            if(!self.userName){
		            self.$Message.success('');
	            }else if(!self.password){
		            self.$Message.success('');
	            }else if(!self.realName){
		            self.$Message.success('');
	            }else if(!self.genger){
		            self.$Message.success('');
	            }else if(self.password!=self.confirmPWD){
                    self.$Message.success('');
                }else{
                    requestObj({
                        url:'regist.do',
                        method:'GET',
                        params:{
                            username:self.userName,
                            realname:self.realName,
                            genger:self.genger,
                            password:self.password,
                        },
                        success:function (res){
                            self.$Message.success('');
                            self.$router.go(-1);
                        },
                        fail:function (err){
                            console.log(err);
                        },
                    });
                }
            },
        }
    }
</script>

<style scoped>

</style>


